'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import Image from 'next/image'
import { createClient } from '@/lib/supabase/client'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import {
  BookOpen,
  Code,
  Trophy,
  Flame,
  TrendingUp,
  Award,
  LogOut,
  ArrowRight,
  CheckCircle2,
  Clock,
} from 'lucide-react'

export const dynamic = 'force-dynamic'

interface Course {
  id: string
  name: string
  description: string
  language: string
  icon_name: string
  color: string
  estimated_hours: number
}

interface CourseProgress {
  course_id: string
  completed_lessons: number
  total_lessons: number
  mastery_score: number
}

export default function StudentDashboard() {
  const [user, setUser] = useState<any>(null)
  const [profile, setProfile] = useState<any>(null)
  const [enrolledCourses, setEnrolledCourses] = useState<Course[]>([])
  const [progress, setProgress] = useState<{ [key: string]: CourseProgress }>({})
  const [streak, setStreak] = useState<any>(null)
  const [badges, setBadges] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const router = useRouter()

  useEffect(() => {
    loadDashboardData()
  }, [])

  async function loadDashboardData() {
    const supabase = createClient()

    try {
      const {
        data: { user: authUser },
        error: userError,
      } = await supabase.auth.getUser()

      if (userError || !authUser) {
        router.push('/auth/login')
        return
      }

      setUser(authUser)

      // Load profile
      const { data: profileData } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', authUser.id)
        .single()

      setProfile(profileData)

      // Load enrolled courses directly
      const { data: enrollmentData } = await supabase
        .from('course_enrollments')
        .select('course_id, courses(*)')
        .eq('student_id', authUser.id)
        .eq('is_active', true)

      console.log('[v0] Course enrollments:', enrollmentData)

      if (enrollmentData) {
        const courses = enrollmentData.map((e: any) => e.courses).filter((c: any) => c)
        setEnrolledCourses(courses)

        // Load progress for each course
        const progressMap: { [key: string]: CourseProgress } = {}
        for (const course of courses) {
          // Get total lessons in course
          const { data: totalLessons } = await supabase
            .from('lessons')
            .select('id, unit_id, units!inner(course_id)')
            .eq('units.course_id', course.id)

          // Get completed lessons
          const { data: progressData } = await supabase
            .from('student_lesson_progress')
            .select('lesson_id, status, lessons(unit_id, units(course_id))')
            .eq('student_id', authUser.id)
            .eq('status', 'completed')

          console.log('[v0] Total lessons:', totalLessons?.length, 'Completed:', progressData?.length)

          const completedInCourse =
            progressData?.filter((p: any) => p.lessons?.units?.course_id === course.id).length || 0

          const totalInCourse = totalLessons?.length || 1

          progressMap[course.id] = {
            course_id: course.id,
            completed_lessons: completedInCourse,
            total_lessons: totalInCourse,
            mastery_score: (completedInCourse / totalInCourse) * 100,
          }
        }
        setProgress(progressMap)
      }

      // Load streak
      const { data: streakData } = await supabase
        .from('student_streaks')
        .select('*')
        .eq('student_id', authUser.id)
        .single()

      setStreak(streakData || { current_streak: 0, longest_streak: 0 })

      // Load badges
      const { data: badgeData } = await supabase
        .from('badges')
        .select('*')
        .eq('student_id', authUser.id)
        .order('earned_at', { ascending: false })
        .limit(5)

      setBadges(badgeData || [])
    } catch (err: any) {
      console.error('[v0] Error loading dashboard:', err)
    } finally {
      setLoading(false)
    }
  }

  async function handleSignOut() {
    const supabase = createClient()
    await supabase.auth.signOut()
    router.push('/')
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center">
        <div className="text-cyan-400 text-lg">Loading dashboard...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black">
      {/* Header */}
      <header className="sticky top-0 z-50 border-b border-white/10 bg-black/80 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-3">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="flex items-center gap-2 group">
              <Image src="/ACL.png" alt="ACL Logo" width={40} height={40} className="w-10 h-10" />
              <div className="hidden sm:block">
                <h1 className="text-sm font-bold text-white leading-tight">Averon</h1>
                <p className="text-xs text-white/75">CodeLab</p>
              </div>
            </Link>
            <Button onClick={handleSignOut} variant="outline" className="gap-2 text-white/80 border-white/10 bg-transparent hover:bg-white/5 text-sm">
              <LogOut className="w-4 h-4" />
              <span className="hidden sm:inline">Sign Out</span>
            </Button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Welcome Section */}
        <div className="mb-12">
          <h1 className="text-4xl font-bold text-white mb-2">
            Welcome back, {profile?.full_name || 'Student'}!
          </h1>
          <p className="text-white/75">Continue your coding journey</p>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-12">
          <Card className="bg-gradient-to-br from-cyan-500/10 to-blue-500/10 border-cyan-500/20">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-white/70">Current Streak</CardTitle>
              <Flame className="w-5 h-5 text-orange-400" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-white">{streak?.current_streak || 0} days</div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-purple-500/10 to-pink-500/10 border-purple-500/20">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-white/70">Courses</CardTitle>
              <BookOpen className="w-5 h-5 text-purple-400" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-white">{enrolledCourses.length}</div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-green-500/10 to-emerald-500/10 border-green-500/20">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-white/70">Badges Earned</CardTitle>
              <Award className="w-5 h-5 text-green-400" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-white">{badges.length}</div>
            </CardContent>
          </Card>

          <Card className="bg-gradient-to-br from-yellow-500/10 to-orange-500/10 border-yellow-500/20">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-white/70">Longest Streak</CardTitle>
              <Trophy className="w-5 h-5 text-yellow-400" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-white">{streak?.longest_streak || 0} days</div>
            </CardContent>
          </Card>
        </div>

        {/* Courses Section */}
        <div className="mb-12">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-white">Your Courses</h2>
            <Link href="/courses">
              <Button variant="outline" className="gap-2 text-white/80 border-white/10 hover:bg-white/5 bg-transparent">
                Browse All Courses <ArrowRight className="w-4 h-4" />
              </Button>
            </Link>
          </div>

          {enrolledCourses.length === 0 ? (
            <Card className="bg-white/5 border-white/10">
              <CardContent className="py-12 text-center">
                <BookOpen className="w-16 h-16 text-white/60 mx-auto mb-4" />
                <h3 className="text-xl font-semibold text-white mb-2">No courses yet</h3>
                <p className="text-white/75 mb-6">Ask your teacher to enroll you in a class</p>
                <Link href="/courses">
                  <Button className="bg-gradient-to-r from-cyan-500 to-blue-500">
                    Explore Courses
                  </Button>
                </Link>
              </CardContent>
            </Card>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              {enrolledCourses.map((course) => {
                const courseProgress = progress[course.id] || {
                  completed_lessons: 0,
                  total_lessons: 1,
                  mastery_score: 0,
                }
                const progressPercent = (courseProgress.completed_lessons / courseProgress.total_lessons) * 100

                return (
                  <Card
                    key={course.id}
                    className="bg-gradient-to-br from-white/5 to-transparent border-white/10 hover:border-cyan-500/50 transition-all duration-300"
                  >
                    <CardHeader>
                      <div className="flex items-start justify-between">
                        <div>
                          <CardTitle className="text-xl font-bold text-white mb-2">{course.name}</CardTitle>
                          <CardDescription className="text-white/75">{course.description}</CardDescription>
                        </div>
                        <div className={`p-3 rounded-xl bg-gradient-to-br from-${course.color}-500/20 to-${course.color}-600/20`}>
                          <Code className={`w-6 h-6 text-${course.color}-400`} />
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-4">
                        <div>
                          <div className="flex justify-between text-sm mb-2">
                            <span className="text-white/70">Progress</span>
                            <span className="text-white font-semibold">{Math.round(progressPercent)}%</span>
                          </div>
                          <Progress value={progressPercent} className="h-2" />
                        </div>
                        <div className="flex items-center justify-between">
                          <div className="text-sm text-white/75">
                            {courseProgress.completed_lessons} / {courseProgress.total_lessons} lessons
                          </div>
                          <Link href={`/courses/${course.id}`}>
                            <Button size="sm" className="bg-gradient-to-r from-cyan-500 to-blue-500 gap-2">
                              Continue <ArrowRight className="w-4 h-4" />
                            </Button>
                          </Link>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                )
              })}
            </div>
          )}
        </div>

        {/* Recent Badges */}
        {badges.length > 0 && (
          <div>
            <h2 className="text-2xl font-bold text-white mb-6">Recent Achievements</h2>
            <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
              {badges.map((badge) => (
                <Card key={badge.id} className="bg-white/5 border-white/10 hover:bg-white/10 transition-colors">
                  <CardContent className="py-6 text-center">
                    <Trophy className="w-10 h-10 text-yellow-400 mx-auto mb-3" />
                    <h3 className="font-semibold text-white text-sm mb-1">{badge.badge_name}</h3>
                    <p className="text-xs text-white/75">{badge.badge_description}</p>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
        )}
      </main>
    </div>
  )
}
